.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.establishment {
    display             : flex;
    align-items         : center;
    background-color    : gray;
    padding             : 10px;
    margin              : 5px 30px;
    border-radius       : 8px;
}

.establishment button {
    background  : none;
    border      : none;
    color       : white;
}

.establishment-description {
    flex : 0.7;
}

.establishmentLikeDislike {
    flex            : 0.2;
    display         : flex;
    flex-direction  : column;
    align-items     : center;
}

.establishmentLikeDislike button {
    background  : none;
    border      : none;
    color       : white;
}

.establishment-favori {
    flex : 0.1;
}




.favoriIcon {
    color: yellow;
}
